#TomatoList{
  margin-top: 16px;
  border-top: 1px solid #ddd;
}
  #TomatoList .dailyTomatoes{
    padding-top: 16px;
  }
  #TomatoList .dailyTomatoes .title{
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: justify;
          justify-content: space-between;
      -ms-flex-align: center;
          align-items: center;
      margin-bottom: 4px;
    }
  #TomatoList .dailyTomatoes .title .dateTime{
        color: #999;
        font-size: 16px;
      }
