        color: #666;
        font-size: 12px;
      }
  #TomatoList .dailyTomatoes .TomatoItem{
      margin: 4px 0;
      font-size: 14px;
    }
  #TomatoList .dailyTomatoes .TomatoItem .timeRange{
        color: #999999;
      }
  #TomatoList .dailyTomatoes .TomatoItem .description{
        color: #222222;
        margin-left: 8px;
      }


