#TodoItem{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 8px 0 8px 8px;
  border-bottom: 1px solid #ddd
}
#TodoItem:hover{
  background: #f9f9f9
}
#TodoItem.editing{
  background: #fff3d2
}
#TodoItem:first-child{
  border-top: 1px solid #ddd;
}
#TodoItem.completed >.text{
      text-decoration: line-through;
      color: #a9a9a9;
}
#TodoItem >.editing,#TodoItem >.text{
    -ms-flex: 1 1;
        flex: 1 1;
}
#TodoItem >.editing{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 8px;
}
#TodoItem >.editing >input{
      border: 0;
      padding: 0;
      outline: none;
      background: transparent;
    }
#TodoItem >.editing >.iconWrapper>.anticon{
      margin-left:4px;
      color: #a9a9a9;
      cursor: pointer;
    }


